/* adv section*/
.advantages__title{
    font-size: 55px;
}
.advantages__subtitle{
    font-size: 36px;
    padding: 0;
}

.advantages hr {
    opacity:.4;
}

.advantages__card--title,
.advantages__card--info{
    font-size: 16px;
    color:#54667a;

}
.advantages__card--title{
    font-weight: bold;
}


@media screen and (max-width: 1024px) {
    .advantage__img{
        float: left;
        padding: 3%;
        vertical-align:middle;
    }
    .call .container {
        flex-direction: column;
    }

    .call__button, .call__text,.call__number {
        width:100%;
        margin-bottom:20px;
    }

    footer h5 {
        font-size:15px;
        margin-top:20px;
        margin-bottom:30px;
    }

    footer .col.l3.m6.s12 {
        height:270px;
    }
    .registration__btns {
        display:flex;
        flex-direction:column;
        width:30%;
    }

}
