/* NEWS */

.news {
    padding:100px 0;
}

.news .row.valign-wrapper {
    margin-bottom:60px;
}

.news__subtitle, .news__title {
    margin:0;
    text-transform:uppercase;
}

.news__subtitle {
    font-size:14px;
}

.news__title {
    font-size:30px;
}

.card__image {
    width:100%;
}

.card__title {
    font-size:16px;
    text-transform:uppercase;
    font-weight:bold;
    margin-top:10px;
    margin-bottom:15px;
}

.card__author, .card__date {
    font-size:14px;
    margin-right:30px;
}

.card__link {
    text-transform: uppercase;
}

.faq {
    padding:60px 0;
}

.faq__content {
    width:80%;
    margin:0 auto;
    padding:60px 40px 100px 40px;
}

.faq__content h4 {
    margin:0;
    font-size:14px;
    text-transform:uppercase;
    font-weight:bold;
}

.faq__content h3 {
    margin:25px 0 0 0;
    font-size:30px;
    text-transform:uppercase;
    font-weight:bold;
}

.faq__content hr {
    width:8%;
    margin-top:20px;
    margin-bottom:40px;
    border:1px solid #51b8f2;
}

.prev-q, .next-q {
    width:100%;
}

footer{
    font-size: 12px;
}
footer h5 {
    font-size:15px;
}

footer a {
    color:#a9b7c6;
}

footer a:hover {
    color:#ffffff;
}

hr {
    opacity:0.1;
}

.subscribe-email {
    
    width:76% !important;
    background-color:#3f4e5f !important;
    color:white;
}

.subscribe-submit {
    padding:6% 10%;
    background-position: center center !important;
    background-size: cover !important;
    border:none;
}
p.center {
    margin-bottom: 0;
    padding-bottom: 10px;
}
/* CALL BLOCK */
.call {
    padding:40px 0;
}

.call .container {
    display:flex;
    justify-content:space-between;
    align-content: center;
    flex-wrap:wrap;
}

.call__text {
    width:32%;
}

.call__button {
    width:10%;
}

.call__number {
    width:31%;
}

.call .call__title {
    font-size:24px;
    margin:0;
}

.call h2 {
    margin:0;
    font-size:36px;
}

.call h3 {
    margin:0;
    font-size:14px;
    color:white;
    text-transform: uppercase;
}

.call p {
    margin:0;
}

.call .material-icons {
    font-size:48px;
}

